.intro {
  position: relative;
  background-color: $black;
}

.intro.webp {
  background-image: url("../img/intro-bg.webp");
}

.intro.no-webp {
  background-image: url("../img/intro-bg.jpg");
}

.intro__content-wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  align-content: center;
  flex-grow: 1;
}

.intro__img-wrapper {
  box-sizing: border-box;
  width: 51.06%;
  padding-left: 15px;
  margin-top: -170px;
}

.intro__text-wrapper {
  width: 48.3%;
  padding-top: 80px;
  margin-top: -170px;
}

.intro__lead-title {
  display: block;
  margin-bottom: 15px;
}

.intro__lead-text {
  font-family: $lifehack;
  font-size: 4.6428rem;
  line-height: 1;
  font-weight: 500;
  color: #ffffff;
}

.intro__arrow {
  animation-name: arrow;
  animation-direction: alternate;
  animation-iteration-count: infinite;
  animation-duration: 0.6s;
  animation-timing-function: linear;
}

@keyframes arrow {
  0% {
    transform: translateY(-10px);
  }

  100% {
    transform: translateY(10px);
  }
}

@media (max-width: 960px) {
  .intro__content-wrapper {
    flex-direction: column;
    justify-content: flex-start;
    width: 65.277%;
    margin: 0 auto;
    text-align: center;
  }

  .intro__img-wrapper {
    width: 100%;
    padding-left: 0;
    margin-top: 0;
    margin-bottom: 25px;
  }

  .intro__text-wrapper {
    width: 100%;
    padding-top: 0;
    margin-top: 0;
  }
}

@media (max-width: 768px) {
  .intro__content-wrapper {
    width: 100%;
    margin: 0;
  }

  .intro__lead-title {
    font-size: 4.5rem;
    line-height: 1;
  }

  .intro__lead-text {
    font-size: 3.8571rem;
    line-height: 1.11;
  }
}

@media (max-width: 380px) {
  .intro__lead-text {
    font-size: 3rem;
  }
}


